"use client"

import { useEffect, useState, useMemo } from "react"
import { supabase } from "@/lib/supabase"
import { toast } from "sonner"
import { Review } from "@/lib/types"
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import {
    Tooltip,
    TooltipContent,
    TooltipProvider,
    TooltipTrigger,
} from "@/components/ui/tooltip"
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from "@/components/ui/dialog"
import {
    Check,
    Trash2,
    Star,
    MessageSquare,
    Image as ImageIcon,
    MoreHorizontal,
    XCircle,
    CheckCircle2,
    Clock,
    AlertCircle
} from "lucide-react"
import { cn } from "@/lib/utils"

export function ReviewManager() {
    const [reviews, setReviews] = useState<Review[]>([])
    const [loading, setLoading] = useState(true)
    const [activeTab, setActiveTab] = useState("all")

    const fetchReviews = async () => {
        try {
            const { data, error } = await supabase
                .from("reviews")
                .select("*")
                .order("created_at", { ascending: false })

            if (error) throw error
            setReviews(data || [])
        } catch (error) {
            console.error("Error fetching reviews:", error)
            toast.error("Failed to load reviews")
        } finally {
            setLoading(false)
        }
    }

    useEffect(() => {
        fetchReviews()
    }, [])

    // --- Stats Calculation ---
    const stats = useMemo(() => {
        const total = reviews.length
        const pending = reviews.filter(r => r.status === 'pending').length
        const approved = reviews.filter(r => r.status === 'approved').length
        const totalStars = reviews.reduce((acc, r) => acc + (r.rating || 0), 0)
        const avgRating = total > 0 ? (totalStars / total).toFixed(1) : "0.0"

        return { total, pending, approved, avgRating }
    }, [reviews])

    // --- Actions ---
    const updateStatus = async (id: string, newStatus: "approved" | "rejected") => {
        // Optimistic UI
        const previousReviews = [...reviews]
        setReviews(bg => bg.map(r => r.id === id ? { ...r, status: newStatus } : r))

        try {
            const { error } = await supabase
                .from("reviews")
                .update({ status: newStatus })
                .eq("id", id)

            if (error) throw error

            toast.success(
                newStatus === 'approved'
                    ? "Review approved successfully"
                    : "Review rejected"
            )
        } catch (error) {
            console.error("Error updating review:", error)
            setReviews(previousReviews) // Revert
            toast.error("Failed to update status")
        }
    }

    const deleteReview = async (id: string) => {
        if (!confirm("Are you sure you want to delete this review?")) return

        const previousReviews = [...reviews]
        setReviews(bg => bg.filter(r => r.id !== id))

        try {
            const { error } = await supabase
                .from("reviews")
                .delete()
                .eq("id", id)

            if (error) throw error
            toast.success("Review deleted")
        } catch (error) {
            console.error("Error deleting review:", error)
            setReviews(previousReviews)
            toast.error("Failed to delete review")
        }
    }

    const filteredReviews = useMemo(() => {
        if (activeTab === "all") return reviews
        return reviews.filter(r => r.status === activeTab)
    }, [reviews, activeTab])

    const getStatusBadge = (status: string) => {
        switch (status) {
            case "approved":
                return (
                    <Badge className="bg-green-100 text-green-700 hover:bg-green-100 border-green-200 gap-1 pl-1.5 shadow-none">
                        <CheckCircle2 className="w-3 h-3" />
                        Approved
                    </Badge>
                )
            case "rejected":
                return (
                    <Badge variant="destructive" className="bg-red-100 text-red-700 hover:bg-red-100 border-red-200 gap-1 pl-1.5 shadow-none">
                        <XCircle className="w-3 h-3" />
                        Rejected
                    </Badge>
                )
            default:
                return (
                    <Badge className="bg-yellow-100 text-yellow-700 hover:bg-yellow-100 border-yellow-200 gap-1 pl-1.5 shadow-none">
                        <Clock className="w-3 h-3" />
                        Pending
                    </Badge>
                )
        }
    }

    const getInitials = (name: string) => {
        return name
            .split(' ')
            .map(n => n[0])
            .slice(0, 2)
            .join('')
            .toUpperCase()
    }

    return (
        <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4 duration-500">
            {/* Stats Overview */}
            <div className="grid gap-4 md:grid-cols-3">
                <Card className="border-none shadow-sm bg-gradient-to-br from-white to-zinc-50/50">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-muted-foreground">
                            Total Reviews
                        </CardTitle>
                        <MessageSquare className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.total}</div>
                        <p className="text-xs text-muted-foreground mt-1">
                            All time collected reviews
                        </p>
                    </CardContent>
                </Card>
                <Card className={cn(
                    "border-none shadow-sm transition-all",
                    stats.pending > 0 ? "bg-amber-50/50" : "bg-gradient-to-br from-white to-zinc-50/50"
                )}>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-muted-foreground">
                            Pending Approval
                        </CardTitle>
                        <AlertCircle className={cn(
                            "h-4 w-4",
                            stats.pending > 0 ? "text-amber-500" : "text-muted-foreground"
                        )} />
                    </CardHeader>
                    <CardContent>
                        <div className={cn(
                            "text-2xl font-bold",
                            stats.pending > 0 ? "text-amber-600" : ""
                        )}>
                            {stats.pending}
                        </div>
                        <p className="text-xs text-muted-foreground mt-1">
                            Requires attention
                        </p>
                    </CardContent>
                </Card>
                <Card className="border-none shadow-sm bg-gradient-to-br from-white to-zinc-50/50">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-muted-foreground">
                            Average Rating
                        </CardTitle>
                        <Star className="h-4 w-4 text-yellow-500 fill-yellow-500" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{stats.avgRating}</div>
                        <p className="text-xs text-muted-foreground mt-1">
                            Based on {stats.total} reviews
                        </p>
                    </CardContent>
                </Card>
            </div>

            {/* Filter Tabs & Table */}
            <Tabs defaultValue="all" className="w-full" onValueChange={setActiveTab}>
                <div className="flex items-center justify-between mb-4">
                    <TabsList className="bg-muted/50 p-1">
                        <TabsTrigger value="all" className="px-4">All Reviews</TabsTrigger>
                        <TabsTrigger value="pending" className="px-4 gap-2">
                            Pending
                            {stats.pending > 0 && (
                                <span className="flex h-5 w-5 items-center justify-center rounded-full bg-amber-100 text-xs font-medium text-amber-600">
                                    {stats.pending}
                                </span>
                            )}
                        </TabsTrigger>
                        <TabsTrigger value="approved" className="px-4">Approved</TabsTrigger>
                        <TabsTrigger value="rejected" className="px-4">Rejected</TabsTrigger>
                    </TabsList>
                </div>

                <div className="rounded-xl border bg-white shadow-sm overflow-hidden">
                    <Table>
                        <TableHeader className="bg-zinc-50">
                            <TableRow className="hover:bg-zinc-50">
                                <TableHead className="w-[250px] uppercase tracking-wider text-xs font-semibold py-4">User</TableHead>
                                <TableHead className="w-[140px] uppercase tracking-wider text-xs font-semibold">Rating</TableHead>
                                <TableHead className="uppercase tracking-wider text-xs font-semibold">Comment</TableHead>
                                <TableHead className="w-[100px] uppercase tracking-wider text-xs font-semibold">Images</TableHead>
                                <TableHead className="w-[120px] uppercase tracking-wider text-xs font-semibold">Status</TableHead>
                                <TableHead className="w-[120px] text-right uppercase tracking-wider text-xs font-semibold pr-6">Actions</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {loading ? (
                                [...Array(5)].map((_, i) => (
                                    <TableRow key={i}>
                                        <TableCell colSpan={6}>
                                            <div className="h-12 w-full animate-pulse bg-muted/20 rounded-md" />
                                        </TableCell>
                                    </TableRow>
                                ))
                            ) : filteredReviews.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={6} className="h-32 text-center">
                                        <div className="flex flex-col items-center justify-center text-muted-foreground">
                                            <MessageSquare className="h-8 w-8 mb-2 opacity-20" />
                                            <p>No reviews found</p>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) : (
                                filteredReviews.map((review) => (
                                    <TableRow key={review.id} className="group hover:bg-zinc-50/50 transition-colors">
                                        <TableCell>
                                            <div className="flex items-center gap-3">
                                                <Avatar className="h-9 w-9 border">
                                                    <AvatarFallback className="bg-primary/5 text-primary text-xs font-medium">
                                                        {getInitials(review.name)}
                                                    </AvatarFallback>
                                                </Avatar>
                                                <div className="flex flex-col">
                                                    <span className="font-medium text-sm text-zinc-900">{review.name}</span>
                                                    <span className="text-xs text-muted-foreground">Verified Customer</span>
                                                </div>
                                            </div>
                                        </TableCell>
                                        <TableCell>
                                            <div className="flex items-center gap-0.5">
                                                {[...Array(5)].map((_, i) => (
                                                    <Star
                                                        key={i}
                                                        className={cn(
                                                            "h-3.5 w-3.5",
                                                            i < review.rating
                                                                ? "text-yellow-400 fill-yellow-400"
                                                                : "text-zinc-200 fill-zinc-100"
                                                        )}
                                                    />
                                                ))}
                                            </div>
                                        </TableCell>
                                        <TableCell className="max-w-[400px]">
                                            <Dialog>
                                                <DialogTrigger asChild>
                                                    <p className="text-sm text-zinc-600 line-clamp-1 cursor-pointer group-hover:text-zinc-900 transition-colors hover:underline decoration-zinc-300 underline-offset-4">
                                                        {review.comment}
                                                    </p>
                                                </DialogTrigger>
                                                <DialogContent>
                                                    <DialogHeader>
                                                        <DialogTitle>Review Details</DialogTitle>
                                                    </DialogHeader>
                                                    <div className="space-y-4 pt-4">
                                                        <div className="flex items-center gap-3 p-3 bg-muted/30 rounded-lg">
                                                            <Avatar>
                                                                <AvatarFallback>{getInitials(review.name)}</AvatarFallback>
                                                            </Avatar>
                                                            <div>
                                                                <p className="font-medium">{review.name}</p>
                                                                <div className="flex gap-0.5">
                                                                    {[...Array(5)].map((_, i) => (
                                                                        <Star
                                                                            key={i}
                                                                            className={cn(
                                                                                "h-3 w-3",
                                                                                i < review.rating
                                                                                    ? "text-yellow-400 fill-yellow-400"
                                                                                    : "text-zinc-200"
                                                                            )}
                                                                        />
                                                                    ))}
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <p className="text-sm leading-relaxed text-zinc-600">
                                                            {review.comment}
                                                        </p>
                                                        {review.image_url && (
                                                            <div className="mt-4">
                                                                <img
                                                                    src={review.image_url}
                                                                    alt="Review attachment"
                                                                    className="rounded-lg border max-h-[300px] w-full object-cover"
                                                                />
                                                            </div>
                                                        )}
                                                    </div>
                                                </DialogContent>
                                            </Dialog>
                                        </TableCell>
                                        <TableCell>
                                            {review.image_url ? (
                                                <Dialog>
                                                    <DialogTrigger asChild>
                                                        <div className="relative h-8 w-12 cursor-zoom-in overflow-hidden rounded-md border bg-zinc-100 group-hover:ring-2 ring-primary/10 transition-all">
                                                            <img
                                                                src={review.image_url}
                                                                alt="Review"
                                                                className="h-full w-full object-cover"
                                                            />
                                                        </div>
                                                    </DialogTrigger>
                                                    <DialogContent className="max-w-2xl border-none bg-transparent shadow-none p-0">
                                                        <img
                                                            src={review.image_url}
                                                            alt="Review full"
                                                            className="h-full w-full rounded-lg shadow-2xl"
                                                        />
                                                    </DialogContent>
                                                </Dialog>
                                            ) : (
                                                <span className="text-xs text-zinc-300">-</span>
                                            )}
                                        </TableCell>
                                        <TableCell>
                                            {getStatusBadge(review.status)}
                                        </TableCell>
                                        <TableCell className="text-right pr-6">
                                            <div className="flex items-center justify-end gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                                                {review.status !== 'approved' && (
                                                    <TooltipProvider>
                                                        <Tooltip>
                                                            <TooltipTrigger asChild>
                                                                <Button
                                                                    variant="ghost"
                                                                    size="icon"
                                                                    className="h-8 w-8 text-green-600 hover:text-green-700 hover:bg-green-50"
                                                                    onClick={() => updateStatus(review.id, 'approved')}
                                                                >
                                                                    <Check className="h-4 w-4" />
                                                                </Button>
                                                            </TooltipTrigger>
                                                            <TooltipContent>Approve Review</TooltipContent>
                                                        </Tooltip>
                                                    </TooltipProvider>
                                                )}

                                                {review.status !== 'rejected' && review.status !== 'approved' && (
                                                    <TooltipProvider>
                                                        <Tooltip>
                                                            <TooltipTrigger asChild>
                                                                <Button
                                                                    variant="ghost"
                                                                    size="icon"
                                                                    className="h-8 w-8 text-amber-600 hover:text-amber-700 hover:bg-amber-50"
                                                                    onClick={() => updateStatus(review.id, 'rejected')}
                                                                >
                                                                    <XCircle className="h-4 w-4" />
                                                                </Button>
                                                            </TooltipTrigger>
                                                            <TooltipContent>Reject Review</TooltipContent>
                                                        </Tooltip>
                                                    </TooltipProvider>
                                                )}

                                                <TooltipProvider>
                                                    <Tooltip>
                                                        <TooltipTrigger asChild>
                                                            <Button
                                                                variant="ghost"
                                                                size="icon"
                                                                className="h-8 w-8 text-zinc-400 hover:text-red-600 hover:bg-red-50"
                                                                onClick={() => deleteReview(review.id)}
                                                            >
                                                                <Trash2 className="h-4 w-4" />
                                                            </Button>
                                                        </TooltipTrigger>
                                                        <TooltipContent>Delete Review</TooltipContent>
                                                    </Tooltip>
                                                </TooltipProvider>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </div>
            </Tabs>
        </div>
    )
}
